*{
    padding: 0;
    margin: 0;
  }
ul,li{
    list-style:none
}
.main{
    /* height: 2090px; */
    background-color: #fff;
}
.title{
    margin-right:20px
}
.topic{
    height: 50px;
    padding: 10px;
    background-color: #f6f6f6;
    border-radius: 3px 3px 0 0;
    
}
.cell{

    height:50px;
    overflow:hidden;
    background-color:#fff;
    border-bottom:2px solid #f0f0f0;
    line-height: 50px;
    position: relative;
}
.cell img{
    width:30px;
    height:30px;
    float:left;
    margin-left:15px;
    margin-top:12px
}
.count{
    width:60px;
    float:left;
    margin-left: 10px;
}
.top{
    width: 40px;
    height: 30px;
    margin-top: 12px;
    margin-left: 5px;
    background-color: #80bd01;
    float: left;
    border-radius:3px 3px ;
    
}
.top span{
    position: absolute;
    top:1px;
    left:123px;
    color: #fff;
}
/* .cell a{
    float:left;
    margin-left:10px;
    color:#000;
    overflow:hidden;

} */
.a_title{
    /* float:left; */
    margin-left:10px;
    color:#000;
    overflow:hidden;
    display: inline-block;
    vertical-align: middle;
    text-overflow: ellipsis;
    white-space:nowrap;
    width: 580px;
    font-size: 16px;
    padding-left: 5px;

}
.button{
    width: 500px;
    height: 30px;
    margin-top: 10px;
}
.button div{
    width: 50px;
    height: 30px;
    border: 1px solid #f6f6f6;
    float: left;
    background-color: #fff;
    text-align: center;
    line-height: 30px;
}
.time{
    float: right;
    height: 50px;
    margin-right: 15px;
    line-height: 50px;
}
.heade{
    background-color: #fff;

    border-radius: 3px 3px 0 0;
}
.head1{
    height: 100px;
    border-radius: 3px 3px 0 0;
    background-color: #fff;
    border-bottom: 2px solid gainsboro;
}
.header_title{
    font-size: 22px;
    font-weight: 700;
    margin: 8px 0;
    display: inline-block;
    vertical-align: bottom;
    width: 75%;
    line-height: 130%;
    padding: 10px;

    /* background-color: #fff; */

}
.put_top{
    background: #80bd01;
    padding: 2px 4px;
    border-radius: 3px;
    -webkit-border-radius: 3px;
    -moz-border-radius: 3px;
    -o-border-radius: 3px;
    color: #fff;
    font-size: 12px;
}
.changes{
    font-size: 12px;
    color: #838383;
    overflow: hidden;
}
.changes span{
    margin-left: 10px;
}

.content{
    width: 90%;
    max-width: 1400px;
    min-width: 960px;
    margin: 15px auto;
    min-height: 400px;
}
.sidebar{
    width: 290px;
    font-size: 14px;
    float: right;
    background-color: gold;
    margin-bottom: 20px;
}
.mian_content{
    height: 300px;
    background-color: #fff;
    padding: 0;
    margin-right: 305px;
}
.head{
    height: 40px;
    /* padding: 10px; */
    background-color: #f6f6f6;
    border-radius: 4px;
}
.breadcrumb{
    padding: 8px 15px;
    /* margin: 0 0 20px; */
    background-color: #f5f5f5;
}
.breadcrumb>li, .navbar .brand {
    text-shadow: none;
}
.breadcrumb>li {
    display: inline-block;
    text-shadow: 0 1px 0 #fff;
}
.breadcrumb a {
    color: #80bd01;
}
breadcrumb>.active{
    color: #999;
}
.inner{
    padding: 10px;
    border-radius: 0 0 3px 3px;
    background-color: #fff;
    text-align: center;
}
.control-group{
    margin: 20px 0;
}
.control-group span{
    float: left;
    width: 160px;
    padding-top: 5px;
    text-align: right;
    /* line-height: 25px; */
    font-size: 14px;
    font-weight: 400;
    line-height: 20px;
    /* margin-bottom: 15px; */
}
.control-group input{
    margin-right: 500px;
    font-size: 14px;
    /* float: left; */

    word-break: break-word;
}
.login{
    width: 150px;
    height: 50px;
    color: #fff;
    border-radius: 3px;
    background-color: #08c;
    box-shadow: none;
    margin-top: 50px;
}